@media (max-width: 1200px) {
  .main {
    &__title {
      font-size: 50px;
    }
  }
}
@media (max-width: 767px) {
  .main {
    &__intro {
      flex-direction: column;
    }

    &__title {
      font-size: 35px;
      z-index: 5;
    }
  }
}
